#header {
  height: 185px; }
  #header .row {
    height: 185px; }

h2.site-slogan {
  margin-top: 5px;
  font-size: 1.4em;
  font-family: 'robotoslabregular', Helvetica, Sans-serif;
  color: #2C2C2C; }

/**
 * CTAs
 */
#header .ctas ul {
  font-size: 0; }

#header .ctas li {
  display: inline-block;
  margin-left: 100px; }

#header .ctas li:first-child {
  margin-left: 0; }

